.blue {
  color: #1f70fb;
}
.blueRound {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  background: #1f70fb;
  border-radius: 50%;
  margin-right: 6px;
}
.searchBox {
  width: 656px;
  margin: 0 auto;
  margin-top: 14px;
  font-size: 0;
}
.searchBox .int {
  display: inline-block;
  vertical-align: middle;
  width: 584px;
  height: 44px;
  background: #ffffff;
  border-radius: 4px 0px 0px 4px;
  padding: 0 16px;
  outline: none;
  border: none;
  font-size: 14px;
}
.searchBox .sBtn {
  display: inline-block;
  vertical-align: middle;
  width: 72px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  background: #1f70fb;
  opacity: 1;
  border-radius: 0px 4px 4px 0px;
  font-size: 14px;
  color: white;
}
.resourceBox {
  width: 1200px;
  height: 695px;
  height: calc(100% - 160px);
  margin: 0 auto;
  margin-top: 30px;
  background: #ffffff;
  border-radius: 4px;
}
.resourceBox .res-tit {
  height: 60px;
  line-height: 60px;
  display: flex;
  justify-content: space-between;
  padding: 0 16px 0 40px;
  border-bottom: 1px solid #F0EFF2;
}
.resourceBox .res-tit .tLeft {
  color: #9aa2b5;
}
.resourceBox .res-list {
  height: calc(100% - 60px);
  overflow: auto;
}
.resourceBox .resUl li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px 20px 40px;
  border-bottom: 1px solid #F0EFF2;
}
.resourceBox .resUl li .liLeft .p1 {
  font-size: 18px;
  font-weight: bold;
  color: #4e5666;
}
.resourceBox .resUl li .liLeft .p2 {
  font-size: 14px;
  color: #9aa2b5;
}
.resourceBox .resUl li .liRight span {
  margin-left: 20px;
}
.resourceBox .resUl li .liRight span i {
  margin-right: 6px;
}
.resourceBox .resUl li .liRight .sp1 {
  display: inline-block;
  vertical-align: middle;
  min-width: 80px;
}
.resourceBox .resUl li .liRight .sp2 {
  display: inline-block;
  vertical-align: middle;
  min-width: 120px;
}
